<h2 id="inputnumber-数字输入框" tabindex="-1"><a class="header-anchor" href="#inputnumber-数字输入框" aria-hidden="true">#</a> Inputnumber 数字输入框</h2>
<h3 id="用法" tabindex="-1"><a class="header-anchor" href="#用法" aria-hidden="true">#</a> 用法</h3>
<h4 id="基本用法" tabindex="-1"><a class="header-anchor" href="#基本用法" aria-hidden="true">#</a> 基本用法</h4>
<div class="component-box">
  <div class="component-box-top"><ClientOnly>
    <wd-input-number
      v-model="inputNum"
      max="20"
      min="0"
      size="small"
      @input="handleInputNumber"
      @change="handleChangeNumber"
      type="text"
    ></wd-input-number>
    </ClientOnly>
  </div>
  <div class="component-box-bottom">
    <div class="component-title">数字输入框</div>
    <div class="component-desc">
      数字输入框只能输入数字，可以设置数值的最大值和最小值；
      并且可以通过上下按钮增加或者减小数值
    </div>
  </div>
</div>
